<template>
  <div id="box">
      <span class="goback" @click="$router.back()">&lt;</span>
      <h2 class="search">{{$route.params.title}}</h2>
      <div class="sousuo">
          <zzz></zzz>
      </div>
    <ul class="oul">
      <li v-for="(item,index) in arr4" :key="index">
        <div class="left">
          <img :src="'//elm.cangdu.org/img/'+item.image_path" alt class="left_img" />
        </div>

        <div class="right">
          <div class="right_top">
            <div class="r_left">品牌</div>
            <div class="r_center">名字</div>
            <div class="r_right">保准漂</div>
          </div>
          <div class="right_center">
            <div class="c_left">
              <el-rate :value="item.rating" disabled show-score text-color="#ff6000"></el-rate>
            </div>
            <div class="c_center">月售{{item.recent_order_num}}单</div>
          </div>
          <div class="right_bottom">
            <div class="b_left">20起送/配送费</div>
            <div class="b_right">{{item.distance}}公里/{{item.order_lead_time}}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import zzz from './zzz'
export default {
  components:{
    zzz
  },
  data() {
    return {
      arr4: [],
      latitude: "",
      longitude: "",
      count: 0,
      rate: 3.7
    };
  },
  mounted() {
    this.latiture = localStorage.getItem("item.latitude");
    this.longitude = localStorage.getItem("item.longitude");
    this.getList();
    window.console.log(this.$route.params.title)
  },

  methods: {
    getList() {
      this.$axios({
        url: `https://elm.cangdu.org/shopping/restaurants`,
        params: {
          latitude: this.latiture,
          longitude: this.longitude,
          offset: this.count,
          limit: 10
        }
      }).then(res => {
        this.arr4 = res.data;
      
        this.count = this.count + 10;
      });
    }
  }
};
</script>

