<template>
  <div id="box">
    <div id="change">
      <div class="kind_box" @click="fen">分类</div>
      <div class="kind_box" @click="goon">排序</div>
      <div class="kind_box">筛选</div>
    </div>
    <div id="kind" v-if="fen_switch">
      <div class="left">
        <h3>异国料理</h3>
        <ul>
          <li
            class="left_li"
            v-for="(item,index) in arr2"
            :key="index"
            @click="gokind(index)"
            :class="{active:'index'}"
          >
            <div class="icon">
              <img :src="'https://fuss10.elemecdn.com/'+item.image_url" alt class="icon_img" />
            </div>
            <div class="msg">{{item.name}}</div>
            <div class="icon_count">{{item.count}}</div>
          </li>
        </ul>
      </div>
      <div class="right">
        <ul>
          <li class="right_li" v-for="(item1,index1) in arr3" :key="index1">
            <div class="right_msg">{{item1.name}}</div>
            <div class="right_count">{{item1.count}}</div>
          </li>
        </ul>
      </div>
    </div>
    <div id="rank" v-if="rank_switch">
      <ul>
        <li v-for="(item,index) in rank_arr" :key="index" @click="getList2(index)">{{item}}</li>
      </ul>
    </div>
    
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      arr: [],
      latitude: "",
      longitude: "",
      arr2: [],
      arr3: [],
      count: 0,
      fen_switch: false,
      rank_arr: [
        "智能排序",
        "距离最近",
        "销量最高",
        "起送价最低",
        "配送速度最快",
        "评分最高"
      ],
      rank_switch: false,
      arr4: []
    };
  },
  mounted() {
    this.latiture = localStorage.getItem("item.latitude");
    this.longitude = localStorage.getItem("item.longitude");
    this.getList();
    this.getkind();
    if (this.arr4) {
      this.getList2(0);
    }
  },
  methods: {
    getList() {
      this.$axios({
        url: "https://elm.cangdu.org/shopping/restaurants",
        params: {
          latitude: this.latiture,
          longitude: this.longitude,
          restaurant_category_id: 0
        }
      }).then(res => {
        this.arr = res.data;
      });
    },
    getList2(index) {
      if (index) {
        this.rank_switch = !this.rank_switch;
      } else {
        index = 0;
      }

      this.$axios({
        url: "https://elm.cangdu.org/shopping/restaurants",
        params: {
          latitude: this.latiture,
          longitude: this.longitude,
          restaurant_category_id: 0,
          order_by: index
        }
      }).then(res => {
        this.arr4 = res.data;
      });
    },
    getkind() {
      this.$axios({
        url: "https://elm.cangdu.org/shopping/v2/restaurant/category"
      }).then(res => {
        this.arr2 = res.data;
      });
    },
    gokind(a) {
      this.arr3 = [];
      for (var i = 0; i < this.arr2[a].sub_categories.length; i++) {
        this.arr3.push(this.arr2[a].sub_categories[i]);
      }
    },
    fen() {
      this.gokind(this.count);
      if (this.rank_switch == true) {
        this.rank_switch = false;
      }
      this.fen_switch = !this.fen_switch;
    },
    goon() {
      this.rank_switch = !this.rank_switch;
      if (this.fen_switch == true) {
        this.fen_switch = false;
      }
    }
  }
};
</script>

<style scoped>
#box {
  width: 100%;
  height: 800px;
  border: 1px solid black;
}
#change {
  width: 100%;
  height: 50px;
  border: 1px solid black;
  display: flex;
}

.kind_box {
  flex: 1;
  height: 100%;
  border: 1px solid black;
}
#kind {
  width: 100%;
  height: 800px;
  position: absolute;
  top: 50px;
  z-index: 1;
}
.left,
.right {
  width: 49%;
  float: left;
}
.left {
  /* background-color: rgba(0,0,0,.1) */
  background-color: #f5f5f5;
}
.left_li {
  width: 100%;
  height: 40px;
  margin-top: 5px;
}
.icon {
  width: 20px;
  height: 20px;

  margin-left: 25px;
  margin-top: 10px;
  float: left;
}
.icon img {
  width: 100%;
}
.msg {
  float: left;

  height: 40px;
  text-align: center;
  line-height: 40px;
  margin-left: 20px;
  color: #666;
  font-size: 12px;
}
.icon_count {
  padding: 0px 5px 0px 5px;
  height: 20px;
  background-color: #555;
  opacity: 0.3;
  float: left;
  margin-left: 10px;
  margin-top: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
  line-height: 20px;
}
.right ul {
  list-style: none;
  margin-top: 30px;
}
.right li {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #ccc;
}
.right_msg {
  float: left;
  width: 60%;
  height: 40px;
  margin-left: 10px;
  line-height: 40px;
  color: #666;
}
.right_count {
  float: left;
  width: 20%;
  height: 40px;
  line-height: 40px;
  margin-left: 10px;
  color: #666;
}
#rank {
  width: 100%;
  height: 500px;
  background-color: #ccc;
  position: absolute;
  left: 0;
  top: 55px;
  z-index: 2;
}
#rank li {
  width: 90%;
  height: 40px;
  border-bottom: 1px solid black;
  padding-left: 20px;
  line-height: 40px;
  padding: 10px;
}
</style>