<template>
  <div>
    <div id="header">
      <span @click="$router.push({name:'home'})">&lt;</span>
    </div>
    <div id="search">
      <input
        type="text"
        class="sear_val"
        v-model="val"
        placeholder="输入商家或者美食名称"
        @input="getkeyword"
        @keydown.38="listup"
        @keydown.40="listdown"
        @keycode.13="cmplment"
      />

      <button class="val" @click="getList">搜索</button>
      <div id="foot">搜索历史</div>
      <div id="btom">
        <ul v-for="(item,index) in list" :key="index">
          <li @click="cmplment" :class="{active:index==sugges_active}">{{item.name}}{{index}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      local: "",
      val: "",
      list: [],
      sugges_active: 0
    };
  },
  mounted() {
    this.local =
      localStorage.getItem("item.latitude") +
      "," +
      localStorage.getItem("item.longitude");
  },
  methods: {
    listup() {
      this.sugges_active == 0
        ? (this.sugges_active = this.list.length - 1)
        : this.sugges_active--;
      this.val = this.list[this.sugges_active].name;
    },
    listdown() {
      this.sugges_active == this.list.length - 1
        ? (this.sugges_active = 0)
        : this.sugges_active++;
      this.val = this.list[this.sugges_active].name;
    },
    getList() {
      this.$axios({
        url: `v4/restaurants?geohash=${this.local}&keyword=${this.val}`
      }).then(res => {
        if (res.data.length) {
          this.list = res.data;
        }
      });
    },

    getkeyword() {
      this.$axios({
        url: `https://elm.cangdu.org/v4/restaurants?geohash=${this.local}&keyword=${this.val}`
      }).then(res => {
        if (res.data.length) {
          this.list = res.data;
        }
      });
    },
    cmplment(e) {
      this.val = e.target.innerText;
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
#header {
  width: 100%;
  height: 50px;
  background-color: #3190e8;
}
#header span {
  font-size: 36px;
  color: white;
  margin-left: 5px;
}
#search {
  width: 100%;
  height: 60px;
}
.sear_val {
  display: block;
  width: 70%;
  height: 40px;
  margin: 5px auto;
  float: left;
  border: 1px solid #ccc;
  margin-left: 20px;
  font-size: 17px;
  text-indent: 2rem;
}
.val {
  display: block;
  float: left;
  width: 60px;
  height: 44px;
  margin-top: 5px;
  margin-left: 5px;
  background-color: #3190e8;
  border: 1px solid #3190e8;
  border-radius: 4px;
  color: white;
  font-size: 18px;
}
#btom {
  width: 100%;
  float: left;
}
#btom ul {
  list-style: none;
}
#btom li {
  height: 30px;
  margin-top: 5px;
  text-indent: 3rem;
}
#foot {
  width: 100%;
  height: 50px;
  background-color: hsl(0, 12%, 92%);
  float: left;
}
.active {
  background-color: red;
}
</style>