<template>
  <div>
    <div class="header">{{msg}}</div>

    {{id}}
    <div class="bg">
      <mt-search placeholder="输入学校" v-model="val"></mt-search>
    </div>
    <div class="bottom">
      <mt-button type="primary" size="large" @click="getLocal()">提交</mt-button>
    </div>
    <div class="oul">
      <li
        v-for="(item,index) in arr_local"
        :key="index"
      @click="fn(item.latitude,item.longitude)"
      >{{item.name}}</li>
    </div>
       <!-- @click="$router.push({name:'home',params:{latitude:item.latitude,longitude:item.longitude}})" -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      id: 0,
      msg: "",
      val: "",
      arr_local: [],
      latitude: ""
    };
  },
  mounted() {
    this.id = window.localStorage.getItem("localid");
    this.msg = window.localStorage.getItem("localcity");
  },
  created() {},
  methods: {
    getLocal() {
      this.$axios({
        url: `https://elm.cangdu.org/v1/pois?city_id=${this.id}&keyword=${this.val}`,
        params: {}
      }).then(res => {
        this.arr_local = res.data;
      });
    },
    fn(a,b) {
      this.$router.push({name:"home"})
      window.console.log('a',a)
      window.console.log('b',b)
      localStorage.setItem('item.latitude',a)
      localStorage.setItem('item.longitude',b)
      
    }
  }
};
</script>

<style scoped>
.header {
  width: 100%;
  height: 40px;
  background-color: #3190e8;
}
.bg {
  width: 100%;
  height: 50px;
}
.bottom {
  width: 100%;
  height: 50px;
}
.oul {
  width: 100%;

  height: 500px;
  position: absolute;
  left: 0;
  top: 152px;
  z-index: 999;
  list-style: none;
}

/* .oul ul{
    width: 100%;
} */
.oul li {
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #ccc;
  line-height: 40px;
  text-align: center;
  margin-top: 5px;
}
</style>