<template>
  <div id="box">
    <mt-swipe :auto="0" :show-indicators="false" ref="swiper" @change="handleChange">
      <mt-swipe-item>
        <ul id="oul">
          <li class="oli" v-for="(item,index) in arr_1" :key="index" @click="$router.push({name:'zzz',params:{id:index,name:item}})" >
            <i class="smallbox" >
              <img :src="'https:fuss10.elemecdn.com'+item.image_url" alt />
            </i>
            <span>{{item.title}}</span>
          </li>
        </ul>
      </mt-swipe-item>
      <mt-swipe-item>
        <ul id="oul">
          <li class="oli" v-for="(item,index) in arr_2" :key="index">
            <i class="smallbox" >
              <img :src="'https:fuss10.elemecdn.com'+item.image_url" alt />
            </i>
            <span>{{item.title}}</span>
          </li>
        </ul>
      </mt-swipe-item>
    </mt-swipe>
    <div class="obtn">
      <span class="swiper_btn" :class="{swiper_btn_checked:color}"></span>
      <span class="swiper_btn" :class="{swiper_btn_checked:!color}"></span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      arr: [],
      arr_1: [],
      arr_2: [],
      color: false,
    };
  },
  mounted() {
    this.fn();
  },
  
  methods: {
    fn() {
      this.$axios({
        url: "v2/index_entry"
      }).then(res => {
        this.arr = res.data;
        this.transArr(this.arr);
      });
    },

    transArr(arr) {
      for (let i = 0; i < arr.length; i++) {
        if (i < 8) {
          this.arr_1.push(arr[i]);
        } else {
          this.arr_2.push(arr[i]);
        }
      }
    },
    handleChange(index) {
      this.$refs.swiper.index = index;
      this.color = !this.color;
    }
  }
};
</script>

<style scoped>
#box {
  width: 100%;
  height: 230px;
 
  position: relative;
}
#oul {
  width: 100%;
  height: 200px;
  list-style: none;
}
.oli {
  width: 24%;
  height: 49%;

  float: left;
}
.oli .smallbox {
  display: block;
  width: 50px;
  height: 50px;

  margin-left: 17px;
  margin-top: 15px;
}
.oli span {
  display: block;
  text-align: center;

  margin-top: 5px;
  color: #666;
}
.oli .smallbox img {
  width: 100%;
  height: 100%;
}
.obtn {
  width: 50px;
  height: 20px;

  position: absolute;
  left: 43%;
  top: 200px;
}
.obtn span {
  display: block;
  width: 10px;
  height: 10px;

  float: left;
  margin-left: 8px;
  border-radius: 10px;
}
.obtn .swiper_btn {
  background-color: #007aff;
}
.obtn .swiper_btn_checked {
  background-color: #2e343b;
}
</style>