<template>
  <div id="box">{{msg}}</div>
</template>

<script>
export default {
  data() {
    return {
      latiture: "",
      longitude: "",
      msg: ""
    };
  },
  mounted() {
    this.latiture = localStorage.getItem('item.latitude')
     this.longitude = localStorage.getItem('item.longitude')
    this.getlocal();
  },
  methods: {
    getlocal() {
      this.$axios({
        url: `https://elm.cangdu.org/v2/pois/${this.latiture},${this.longitude}`
      }).then(res => {
        this.msg = res.data.address;
        window.console.log(res,'定位');
      });
    }
  }
};
</script>

<style scoped>
#box {
  width: 100%;
  height: 50px;
  background-color: #3190e8;
  text-align: center;
  line-height: 50px;
  color: white;
}
</style>