<template>
  <div id="box">
    <div id="header">
      <div class="Header">饿了么</div>
    </div>
    <div id="header_local">
      <div class="Header">{{local_msg}}</div>
    </div>
    <div id="header_localcity">
      <div class="Header">{{msg}}</div>
    </div>

    <div id="city_hot">
      <div class="hot_top">热门城市</div>
      <ul>
        <!-- <li v-for="(item,index) in hot_city" :key="index" @click="$router.push({name:'city',params:{name:item.name,id:item.id}})">{{item.name}}</li> -->
        <li
          v-for="(item,index) in hot_city"
          :key="index"
          @click="fn(item.id,item.name)"
        >{{item.name}}</li>
      </ul>
    </div>
    <div id="city_A">
      <div class="hot_top">A</div>
      <ul>
        <li v-for="(item,index) in A" :key="index">{{item.name}}</li>
      </ul>
    </div>
    <div id="city_B">
      <div class="hot_top">b</div>
      <ul>
        <li v-for="(item,index) in B" :key="index">{{item.name}}</li>
      </ul>
    </div>
    <div id="city_C">
      <div class="hot_top">c</div>
      <ul>
        <li v-for="(item,index) in C" :key="index">{{item.name}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      msg: "定位中",
      id: 0,
      local_msg: "当前定位城市",
      hot_city: [],
      all_city: [],
      A: [],
      B: [],
      C: []
    };
  },
  mounted() {
    this.getList();
    this.getHotCity();
    this.getAllCity();
  },
  methods: {
    getList() {
      this.$axios({
        url: "https://elm.cangdu.org/v1/cities?type=guess",
        params: {}
      }).then(res => {
        this.msg = res.data.name;
      });
    },
    getHotCity() {
      this.$axios({
        url: "https://elm.cangdu.org/v1/cities?type=hot",
        params: {}
      }).then(res => {
        this.hot_city = res.data;
      });
    },
    getAllCity() {
      this.$axios({
        url: "https://elm.cangdu.org/v1/cities?type=group",
        params: {}
      }).then(res => {
        this.all_city = res.data;
        this.A = res.data.A;
        this.B = res.data.B;
        this.C = res.data.C;
      });
    },
    fn(a, b) {
      this.$router.push({ name: "city" });
      localStorage.setItem("localcity", b);
      localStorage.setItem("localid", a);
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
#box {
  width: 100%;
  height: 100%;
}
#box {
  width: 100%;
  height: 100%;
}
#header {
  width: 100%;
  height: 51px;
  background-color: #3190e8;
}
#header .Header {
  width: 30%;
  height: 51px;
  color: aliceblue;
  text-align: center;
  line-height: 51px;
}
#header_local {
  width: 98%;
  height: 51px;
  border: 1px solid #c1c1c1;
  line-height: 51px;
  color: #666;
}
#header_localcity {
  width: 98%;
  height: 51px;
  border: 1px solid #c1c1c1;
  color: #3190e8;
  line-height: 51px;
  font-size: 26px;
}
#city_hot {
  width: 100%;
  display: block;
  background-color: #3190e8;
  background-color: red;
}
#city_hot .hot_top {
  width: 100%;

  background-color: blue;
}
#city_hot ul {
  width: 100%;

  background-color: white;
  list-style: none;
}
#city_hot li {
  width: 24%;
  height: 30px;
  border: 1px solid #ccc;
  float: left;
  line-height: 30px;
  text-align: center;
  color: #3190e8;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
#city_A {
  width: 100%;
  background-color: #666;
}
#city_A li,
#city_B li,
#city_C li {
  width: 24%;
  height: 30px;
  border: 1px solid #ccc;
  float: left;
  line-height: 30px;
  text-align: center;
  color: #3190e8;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
</style>