<template>
  <div id="box">
    <div id="left" ref="left">
      <ul>
        <li
          v-for="(item,index) in arr_right"
          :key="index"
          @click="changeN(index)"
          :class="{l_checked:index==n}"
        >{{item.name}}</li>
      </ul>
    </div>
    <div id="right" ref="right">
      <div class="right_box">
        <div v-for="(item,index) in arr_right" :key="index" class="r_box" :id="'goright'+index">
          <h3>{{item.name}}</h3>
          <ul>
            <li v-for="(item1,index1) in item.list" :key="index1">
              <span>{{item1.name}}</span>
              <b>+</b>
              <i>$:{{item1.price}}</i>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  data() {
    return {
      n: 0,
      arr_right: [
        {
          name: "热销榜",
          list: [
            { name: "鲜榨系列", price: 12, type: "热销", count: 1 },
            { name: "法式奶霜红/绿茶", price: 55, type: "热销", count: 1 },
            { name: "红茶拿铁", price: 868, type: "热销", count: 1 },
            { name: "珍珠红茶拿铁", price: 43, type: "热销", count: 1 }
          ]
        },
        {
          name: "优惠",
          list: [
            { name: "南非国宝拿铁", price: 88, type: "优惠", count: 2 },
            { name: "芋头牛奶", price: 63, type: "优惠", count: 2 },
            { name: "牛奶双重奏", price: 77, type: "优惠", count: 2 },
            { name: "招牌咖啡", price: 867, type: "优惠", count: 2 }
          ]
        },
        {
          name: "奶盖",
          list: [
            { name: "巧克力奶茶", price: 78, type: "奶盖", count: 3 },
            { name: "巧克力珍珠奶茶", price: 12, type: "奶盖", count: 3 },
            { name: "闲情鲜品", price: 21, type: "奶盖", count: 3 }
          ]
        },
        {
          name: "养乐多",
          list: [
            { name: "醇品绿茶", price: 121, type: "养乐多", count: 4 },
            { name: "庄园红茶", price: 574, type: "养乐多", count: 4 },
            { name: "法式奶霜红/绿茶", price: 55, type: "养乐多", count: 4 }
          ]
        },
        {
          name: "果茶",
          list: [
            { name: "红茶拿铁", price: 868, type: "果茶", count: 5 },
            { name: "珍珠红茶拿铁", price: 43, type: "果茶", count: 5 },
            { name: "南非国宝拿铁", price: 88, type: "果茶", count: 5 }
          ]
        },
        {
          name: "仙茶",
          list: [
            { name: "芋头牛奶", price: 63, type: "仙茶", count: 6 },
            { name: "牛奶双重奏", price: 77, type: "仙茶", count: 6 },
            { name: "招牌咖啡", price: 867, type: "仙茶", count: 6 }
          ]
        },
        {
          name: "冰沙",
          list: [
            { name: "巧克力奶茶", price: 78, type: "冰沙", count: 7 },
            { name: "巧克力珍珠奶茶", price: 12, type: "冰沙", count: 7 },
            { name: "闲情鲜品", price: 21, type: "冰沙", count: 7 }
          ]
        },
        {
          name: "鲜榨果汁",
          list: [
            { name: "醇品绿茶", price: 121, type: "鲜榨果汁", count: 8 },
            { name: "庄园红茶", price: 574, type: "鲜榨果汁", count: 8 },
            { name: "法式奶霜红/绿茶", price: 55, type: "鲜榨果汁", count: 8 }
          ]
        },
        {
          name: "纯奶",
          list: [
            { name: "红茶拿铁", price: 868, type: "纯奶", count: 9 },
            { name: "珍珠红茶拿铁", price: 43, type: "纯奶", count: 9 },
            { name: "南非国宝拿铁", price: 88, type: "纯奶", count: 9 }
          ]
        },
        {
          name: "热饮",
          list: [
            { name: "芋头牛奶", price: 63, type: "热饮", count: 10 },
            { name: "牛奶双重奏", price: 77, type: "热饮", count: 10 },
            { name: "招牌咖啡", price: 867, type: "热饮", count: 10 },
            { name: "巧克力奶茶", price: 78, type: "热饮", count: 10 }
          ]
        },
        {
          name: "加料",
          list: [
            { name: "巧克力珍珠奶茶", price: 12, type: "加料", count: 11 },
            { name: "闲情鲜品", price: 21, type: "加料", count: 11 },
            { name: "醇品绿茶", price: 121, type: "加料", count: 11 },
            { name: "庄园红茶", price: 574, type: "加料", count: 11 }
          ]
        }
      ]
    };
  },
  mounted() {
    var ArrHeight = [0];
    var height = 0;
    var divs = document.querySelectorAll(".r_box");
    for (let i = 0; i < divs.length; i++) {
      height += divs[i].offsetHeight;

      ArrHeight.push(height);
    }

    this.scroll_left = new BScroll(this.$refs.left, {
      scrollY: true,
      click: true,
      probeType: 2
    });
    this.scroll_right = new BScroll(this.$refs.right, {
      scrollY: true,
      click: true,
      probeType: 2
    });

    this.scroll_right.on("scroll", pos => {
      let posY = Math.abs(Math.round(pos.y));
      for (let i = 0; i < ArrHeight.length - 1; i++) {
        if (posY >= ArrHeight[i] && posY < ArrHeight[i + 1]) {
          this.n = i;
        }
      }
    });
  },
  methods: {
    changeN(ind) {
      this.n = ind;
      this.scroll_right.scrollToElement("#goright" + ind, 1000);
    }
  }
};
</script>

<style scoped>
#box {
  width: 100%;
  height: 100%;
  display: flex;
}
#left {
  width: 25%;
  height: 100%;
  background-color: red;
  overflow: hidden;
}
#left ul {
  list-style: none;
  height: 160%;
}
#left li {
  width: 100%;
  height: 3rem;
  text-align: center;
  line-height: 3rem;
  border-bottom: 1px solid black;
}
#left .l_checked {
  background-color: aliceblue;
}
#right {
  flex: 1;
  height: 100%;
  background-color: aqua;
}
#right ul {
  list-style: none;
}
#right .right_box {
  width: 100%;
}
.r_box {
  width: 100%;

  margin-top: 1rem;
}
.r_box li {
  width: 100%;
  height: 4rem;
  border: 1px solid black;
  position: relative;
}
.r_box li span {
  display: block;
  font-size: 1.2rem;
  text-align: center;
  line-height: 2rem;
}
.r_box li b {
  font-size: 1rem;
  display: inline-block;
  width: 1rem;
  height: 1rem;
  text-align: center;
  line-height: 0.9rem;
  background-color: red;
  border-radius:100% ;
  position: absolute;
  right: 6rem;
  top: 60%;
  transform: translateY(-60%)
}
.r_box li i{
  position: absolute;
  right: 3rem;
  top: 50%;
   transform: translateY(-40%)
}
</style>