<template>
  <div id="box">
    <div id="detail_box">
      <div class="de_top">
        <div class="top_left">
          <div class="de_top_img">
            <img :src="'//elm.cangdu.org/img/'+list.image_path" alt="666" />
          </div>
        </div>
        <div class="top_right">
          <div class="right_name">
            <h3>{{list.name}}</h3>
          </div>
          <div class="right_methods">
            <span>商家配送</span>/
            <span>分钟送达</span>/
            <span>配送费$5</span>
          </div>
          <div class="right_notice">公告 : {{list.promotion_info}}</div>
        </div>
      </div>
      <div class="de_center">
        <div class="center_">
          <div class="cen_left" v-if="turn_center">
            <div class="cen_left_top">
              <!-- <span :class="left_checked" @click="$router.push({name:'detay'})">商品</span> -->
              <span :class="{left_checked:detail_class==true}" @click="toleft">商品</span>

              <span :class="{left_checked:detail_class==false}" @click="toright">评价</span>
            </div>
            <div class="detail_de">
              <router-view></router-view>
            </div>
          </div>
          <div class="cen_right" v-else>222</div>
        </div>
      </div>
      <div class="de_bottom">
        <div class="shop_car">
          <div class="car">
            <i class="el-icon-shopping-cart-1"></i>
            <span class="car_number" v-if="car_num!=0">{{car_num}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      car_num: 6,
      list: [],
      img: "",
      turn_center: true,
      detail_class: true,
      wrapper:true
    };
  },
  mounted() {
    this.getlist();
    window.console.log(this.de_top);
  },
  methods: {
    getlist() {
      this.$axios({
        url: "https://elm.cangdu.org/shopping/restaurant/1"
      }).then(res => {
        this.list = res.data;
        // window.console.log(res);
        this.img = res.data.image_path;
        // window.console.log('//elm.cangdu.org/img/'+this.img)
      });
    },
    toleft() {
      this.$router.push({ name: "detay" }), (this.detail_class = true);
    },
    toright() {
      this.$router.push({ name: "evalute" }), (this.detail_class = false);
    }
  }
};
</script>

<style scoped>
