  width: 100%;
  height: 15%;
  color: white;
}

.de_top::before {
  content: "5555";
  display: block;
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  filter: blur(5px);
  background-image: url(../img/timg1.jpg);
}
.de_top_img {
  width: 100%;
  height: 100%;
}
.de_top_img img {
  width: 100%;
  height: 100%;
}
.de_center {
  width: 100%;
  flex: 1;
  overflow: auto;
  /* background-color: green; */
  background-color: aliceblue
}
.de_bottom {
  width: 100%;
  height: 11%;
  background-color: #333;
  position: relative;
}
.shop_car {
  width: 80px;
  height: 80px;
  border-radius: 100%;
  position: absolute;
  left: 20px;
  top: -20px;
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
}
.car {
  width: 80%;
  height: 80%;

  border-radius: 100%;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3190e8;
}
.car i {
  font-size: 45px;
  color: white;
}
.car_number {
  width: 20px;
  height: 20px;
  background-color: red;
  position: absolute;
  right: 0;
  top: -10px;
  border-radius: 20px;
  text-align: center;
  line-height: 20px;
  color: white;
}
.top_left {
  width: 30%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  float: left;
}
.de_top_img {
  width: 80%;
  height: 80%;
  border: 1px solid black;
}
.top_right {
  width: 70%;
  height: 100%;

  float: left;
}
.right_name {
  margin-top: 10px;
  padding-left: 10px;
}
.right_methods {
  padding-left: 10px;
  font-size: 10px;
  margin-top: 5px;
}
.right_notice {
  padding-left: 10px;

  font-size: 10px;
  margin-top: 5px;
}
.center_{
  width: 100%;
  height: 100%;
}
.cen_left {
  height: 100%;
  width: 100%;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
  flex: 1
}
.cen_left_top {
  width: 100%;
  height: 60px;
