import Router from "vue-router"
import Vue from "vue"
import hello from "../components/HelloWorld"
import index from "../components/local_index"
import local from "../components/local"
import city from "../components/city"
import home from "../components/home"
import serch from "../components/serch"
import search from "../components/search"
import zzz from "../components/zzz"
import banner from "../components/banner"
import list from "../components/List"
import listsearch from "../components/List_search"
import test from "../components/test"
import detail from "../components/detail"
import detail_commodetay from "../components/detail_commodetay"
import detail_evalute from "../components/detail_evalute"


Vue.use(Router)

const router =new Router({
    mode:"history",
    routes:[
        {
            path:"/local",
            component:local,
            name:"local"
        },
      
        {
            path:"/index",
            component:index,
            name:"index"
        },
        
        {
            path:"/city",
            component:city,
            name:"city"
        }
        ,
        {
            path:"/home",
            component:home,
            name:"home"
        }
        ,
        {
            path:"/serch",
            component:serch,
            name:"serch"
        }
        ,
        {
            path:"/search",
            component:search,
            name:"search"
        },
        {
            path:"/zzz",
            component:zzz,
            name:"zzz"
        },
        {
            path:"/banner",
            component:banner,
            name:"banner"
        },
        {
            path:"/list",
            component:list,
            name:"list"
        },
        {
            path:"/list2",
            component:listsearch,
            name:"list2"
        } , {
            path:"/hello",
            component:hello,
            name:"hello"
        },{
            path:"/test",
            component:test,
            name:"test"
        },
        {
            path:"/detail",
            component:detail,
            name:"detail",
            children:[
                {
                    path:'/detail/detay',
                    component:detail_commodetay,
                    name:'detay'
                },
                {
                    path:'/detail/evalute',
                    component:detail_evalute,
                    name:'evalute'
                }
            ]
        }
    ]
})

export default router